import { motion } from "framer-motion";

const Header = () => {
  return (
    <motion.div
      initial={{ opacity: 0, scale: 0.5 }}
      animate={{ opacity: 1, scale: 1 }}
      exit={{ opacity: 0, scale: 0.5 }}
      transition={{ duration: 1, ease: "anticipate" }}
      className="header"
    >
      <h1>Mr AK</h1>
      <p>Editing</p>
    </motion.div>
  );
};

export default Header;
