import { Link } from "react-router-dom";

const Navbar = () => {
  const RemoveClass = () => {};

  return (
    <nav className="navbar navbar-expand-lg navbar-light bg-light" id="nav">
      <div className="container-fluid">
        <Link className="navbar-brand" to="/">
          <img src="images/logo.png" alt="mrAKEdits" />
        </Link>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
            <li className="nav-item">
              <Link className="nav-link" to="/" onClick={RemoveClass}>
                Home
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/posts" onClick={RemoveClass}>
                Thumbnails
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/videos" onClick={RemoveClass}>
                Videos
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/about" onClick={RemoveClass}>
                About Us
              </Link>
            </li>
            <li className="nav-item">
              <a
                className="nav-link"
                href="https://www.instagram.com/mrakediting/"
                target="_ayan"
                onClick={RemoveClass}
              >
                Instagram
              </a>
            </li>
            {localStorage.getItem("admin") === "mrak1" && (
              <li className="nav-item">
                <Link to="/admin" className="nav-link" onClick={RemoveClass}>
                  Admin
                </Link>
              </li>
            )}
          </ul>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
